import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-search';

const Search = forwardRef(({
  children,
  className,
  container,
  dynamic,
  expandable,
  navbar,
  role,
  ...remainingProps
}, ref) => (
  <div
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--container`]: Boolean(container),
      [`${CSS_PREFIX}--dynamic`]: Boolean(dynamic),
      [`${CSS_PREFIX}--expandable`]: Boolean(expandable),
      [`${CSS_PREFIX}--nav-bar`]: Boolean(navbar)
    }, className)}
    ref={ref}
    role={role}
    {...remainingProps}
  >
    {children}
  </div>
));

Search.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Container Search bar
   */
  container: bool,

  /**
   * Dynamic Search bar
   */
  dynamic: bool,

  /**
   * Expandable Search bar
   */
  expandable: bool,

  /**
   * Navbar Search bar
   */
  navbar: bool,

  /**
   * @ignore
   */
  role: string
};

Search.displayName = 'Search';

Search.defaultProps = {
  role: 'search'
};

export default Search;
