/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable jsx-a11y/anchor-has-content */

import React, { useState } from 'react';
import { arrayOf, func, object, shape, string } from 'prop-types';
import classnames from 'classnames';
import Downshift from 'downshift';
import ButtonIcon from '../../button-icon';
import Icon from '../../icon';
import Input from '../../input';
import Search from '../../search';
import SearchButton from '../../search-button';
import Listbox from '../../listbox'
import ListboxItem from '../../listbox-item'

const SearchCombobox = ({ components, history, setSearchQuery, inputContainerProps }) => {
  const [value, setValue] = useState({ name: '' });

  const handleStateChange = changes => {
    if (changes.hasOwnProperty('selectedItem')) {
      setValue(changes.selectedItem);
    } else if (changes.hasOwnProperty('inputValue')) {
      setValue({ name: changes.inputValue })
    }
  }

  const handleSubmit = e => {
    e.preventDefault();
    setSearchQuery(e.target.elements.q.value.trim());
    history.push(`${process.env.PUBLIC_URL}/search`);
  };

  return (
    <form onSubmit={handleSubmit} role="search">
      <Downshift
        itemToString={item => (item ? item.name : '')}
        onStateChange={handleStateChange}
        selectedItem={value}
      >
        {({
          getInputProps,
          getItemProps,
          getMenuProps,
          getRootProps,
          highlightedIndex,
          inputValue,
          isOpen,
          selectedItem,
          ...rest
        }) => (
          <Search
            role={undefined}
          >
            <Input
              {...getInputProps()}
              autoCorrect="off"
              autoCapitalize="none"
              aria-label="Site-wide"
              label="Search components"
              labelId={getInputProps()['aria-labelledby']}
              inputContainerProps={{
                ...inputContainerProps,
                'aria-expanded': isOpen ? 'true' : 'false'
              }}
              name="q"
              placeholder="Search components"
              type="search"
              {...getRootProps({}, { suppressRefError: true })}
              useNativePlaceholder
            />
            <div
              className={classnames('vds-menu--absolute', {
                [`vds-menu--show`]: isOpen && inputValue.length && components.filter(item => item.name.match(inputValue)).length
              })}
            >
              <Listbox {...getMenuProps()}>
                {
                  isOpen
                  ? components.filter(item => item.name.match(inputValue)).map((item, index) => (
                    <ListboxItem
                      active={highlightedIndex === index}
                      key={index}
                      selected={selectedItem === item}
                      {...getItemProps({
                        key: index,
                        index,
                        item
                      })}
                    >
                      {item.name}
                    </ListboxItem>
                  ))
              : null}
              </Listbox>
            </div>
            <SearchButton>
              <ButtonIcon type="submit" ariaLabel="search" iconType="light">
                <Icon name="search" resolution="low" />
              </ButtonIcon>
            </SearchButton>
          </Search>)}
      </Downshift>
    </form>
  );
};

SearchCombobox.defaultProps = {
  inputContainerProps: {
    role: 'combobox',
    'aria-haspopup': 'listbox'
  }
};

SearchCombobox.propTypes = {
  /**
   * List of components
   */
  components: arrayOf(shape),

  /**
   * History
   */
  history: object,

  /**
   * Set search query
   */
  setSearchQuery: func,

  inputContainerProps: shape({
    role: string,
    'aria-haspopup': string,
    'aria-expanded': string
  })
};

export default SearchCombobox;
