import React from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-nav-site';
const CSS_STATE = 'vds-state';

const NavSite = ({ children, className, show, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--show`]: Boolean(show)
    }, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

NavSite.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Show in mobile
   */
  show: bool
};

NavSite.displayName = 'NavSite';

export default NavSite;
