/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable jsx-a11y/anchor-has-content */

import React, { useRef, useState } from 'react';
import ButtonIcon from '../../button-icon';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Input from '../../input';
import Nav from '../../nav';
import NavAction from '../../nav-action';
import NavActionInfo from '../../nav-action-info';
import NavBrand from '../../nav-brand';
import NavBrandInfo from '../../nav-brand-info';
import NavBrandLogo from '../../nav-brand-logo';
import NavBrandLogoImage from '../../nav-brand-logo-image';
import NavLinks from '../../nav-links';
import NavSite from '../../nav-site';
import NavSkip from '../../nav-skip';
import Row from '../../row';
import Search from '../../search';
import SearchButton from '../../search-button';
import SearchClearButton from '../../search-clear-button';
import SearchIcon from '../../search-icon';
import Tab from '../../tab';
import TabList from '../../tab-list';
import Typography from '../../typography';
import SearchCombobox from './search-combobox';
import Logo from '@visa/vds-web/dist/_logos/visa/visa_vbm_wht.svg';
import  './index.css';

const SearchExample = (props) => {
  const refOne = useRef();
  const refTwo = useRef();
  const refThree = useRef();
  const refFour = useRef();

  const [stateOne, setStateOne] = useState({
    disableToggle: false,
    expanded: false,
    value: ''
  });

  const [stateTwo, setStateTwo] = useState({
    disableToggle: false,
    expanded: false,
    value: ''
  });

  const [stateThree, setStateThree] = useState({
    value: ''
  });

  const [stateFour, setStateFour] = useState({
    value: ''
  });

  const handleClickOne = () => {
    if (stateOne.value === '' && stateOne.disableToggle === false) {
      refOne.current.focus();

      setStateOne({
        ...stateOne,
        disableToggle: true,
        expanded: !stateOne.expanded
      });
    }
  };

  const handleBlurOne = () => {
    setStateOne({
      ...stateOne,
      expanded: Boolean(stateOne.value.length)
    });

    setTimeout(() => setStateOne({
      ...stateOne,
      disableToggle: false,
      expanded: Boolean(stateOne.value.length)
    }), 300);
  };

  const handleClickTwo = () => {
    if (stateTwo.value === '' && stateTwo.disableToggle === false) {
      refTwo.current.focus();

      setStateTwo({
        ...stateTwo,
        disableToggle: true,
        expanded: !stateTwo.expanded
      });
    }
  };

  const handleBlurTwo = () => {
    setStateTwo({
      ...stateTwo,
      expanded: Boolean(stateTwo.value.length)
    });

    setTimeout(() => setStateTwo({
      ...stateTwo,
      disableToggle: false,
      expanded: Boolean(stateTwo.value.length)
    }), 300);
  };

  const handleClickThree = () => {
    refThree.current.focus();
    setStateThree({
      ...stateThree,
      value: ''
    })
  };

  const handleClickFour = () => {
    refFour.current.focus();
    setStateFour({
      ...stateFour,
      value: ''
    })
  };
  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Search Default with interactive/a11y functionality</Typography>
          <Typography>
            This is a simple example of how to wire the search component to showcase how it can be used in
            an application. The different versions of the atomic search component can be used to support different
            application needs for Search.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <SearchCombobox {...props} />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Search Default</Typography>
          <Typography tag="h3">Component Only (no search functionality)</Typography>
          <Typography>
            Standard search interaction provides search results
            after user has input their keyword search query and
            submit the action. While auto-suggest may help the user
            complete their input, search results are not displayed
            until users explicitly submit the search action.
          </Typography>
          <Typography>
            Note: Standard Search includes a button with the
            magnifying glass icon and the input field is 48px tall.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Search aria-label="Demo Example 2">
            <Input
              aria-label="Site-wide"
              name="q"
              placeholder="Search events"
              type="search"
              useNativePlaceholder
            />
            <SearchButton>
              <ButtonIcon ariaLabel="search" iconType="light">
                <Icon name="search" resolution="low" />
              </ButtonIcon>
            </SearchButton>
          </Search>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Search Expandable (with state)</Typography>
          <Typography tag="h3">Component Only (no search functionality)</Typography>
          <Typography>
            When the search feature is more of a secondary action and
            the allowed canvas real-estate is limited, the search magnifying
            glass icon can be used as an entry point to invoke a modal search box.
          </Typography>
          <Typography>
            Once users press the search icon, the text field will expand and be ready for input.
          </Typography>
          <Typography>
            After the field is expanded, if focus is removed from field
            before an entry is made, then the field will collapse to it’s default state.
          </Typography>
          <Typography>
            Pressing the search icon while the field is expanded will check
            if the field contains entered text. If there is an entry then a
            search is performed, otherwise the field is collapsed to it’s default state.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Search aria-label="Demo Example 3" container expandable>
            <Input
              expanded={stateOne.expanded}
              aria-label="Site-wide"
              name="q"
              onBlur={handleBlurOne}
              onChange={e => setStateOne({ ...stateOne, value: e.target.value })}
              placeholder="Search real-estate"
              ref={refOne}
              type="search"
              useNativePlaceholder
              tabIndex={stateOne.expanded ? '0' : '-1'}
              value={stateOne.value}
            />
            <SearchButton>
              <ButtonIcon
                ariaLabel="search"
                iconType={stateOne.expanded ? 'light' : undefined}
                onClick={handleClickOne}
                aria-expanded={stateOne.expanded}
              >
                <Icon name="search" resolution="low" />
              </ButtonIcon>
            </SearchButton>
          </Search>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Navigation Bar With Standard Expandable Search</Typography>
          <Typography tag="h3">Component Only (no search functionality)</Typography>
          <Typography>
            Search feature as placed in the Navigation Bar will gather
            results from across the entire app experience. By default,
            it will use the expandable behavior with non-dynamic search behavior.
            This design of the text field input (no borders) is specific to Search on the Navigation Bar.
          </Typography>
          <Typography>
            Default
          </Typography>
          <Typography>
            By default the user will only see the Search magnifying glass
            icon as part of the navigation bar. To invoke the search feature,
            the user will press on the icon button.
          </Typography>
          <Typography>
            After Expansion
          </Typography>
          <Typography>
            Once the user presses on the Search magnifying glass,
            the search box will expand and place the focus on the text
            field, ready for users to type in their query.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Nav position="relative">
            <Grid>
              <Row>
                <Col>
                  <NavSkip href="#content">Skip to main content</NavSkip>
                  <NavBrand>
                    <NavBrandInfo>
                      <ButtonIcon ariaLabel="menu" iconType="dark">
                        <Icon name="menu" resolution="low" />
                      </ButtonIcon>
                      <NavBrandLogo href="http://www.visa.com">
                        <NavBrandLogoImage alt="Visa" src={Logo} />
                      </NavBrandLogo>
                    </NavBrandInfo>
                  </NavBrand>
                  <NavSite>
                    <NavLinks ariaLabel="Demo Site Navigation">
                      <TabList role="none">
                        <Tab tag={<a />} href="http://www.visa.com" role={undefined}>Contact</Tab>
                        <Tab tag={<a />} href="http://www.visa.com" role={undefined}>Service</Tab>
                        <Tab tag={<a />} href="http://www.visa.com" role={undefined}>Statistics</Tab>
                      </TabList>
                    </NavLinks>
                  </NavSite>
                  <NavAction>
                    <NavActionInfo>
                      <Search aria-label="Demo Example 4" expandable navbar>
                        <Input
                          expanded={stateTwo.expanded}
                          aria-label="Site-wide"
                          name="q"
                          onBlur={handleBlurTwo}
                          onChange={e => setStateTwo({ ...stateTwo, value: e.target.value })}
                          placeholder="Search"
                          ref={refTwo}
                          type="search"
                          tabIndex={stateTwo.expanded ? '0' : '-1'}
                          useNativePlaceholder
                          value={stateTwo.value}
                        />
                        <SearchButton>
                          <ButtonIcon
                            ariaLabel="search"
                            iconType={stateTwo.expanded ? 'light' : 'dark'}
                            onClick={handleClickTwo}
                            aria-expanded={stateTwo.expanded}
                          >
                            <Icon name="search" resolution="low" />
                          </ButtonIcon>
                        </SearchButton>
                      </Search>
                    </NavActionInfo>
                  </NavAction>
                </Col>
              </Row>
            </Grid>
          </Nav>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Dynamic Search</Typography>
          <Typography tag="h3">Component Only (no search functionality)</Typography>
          <Typography>
            Dynamic search begins when the user starts typing in their
            query after invoking the search function. Users will see a
            list of auto-suggestions to help users find the proper query or
            related results. The search-as-you-type is often used in dedicated
            search experiences and often in mobile app experiences.
          </Typography>
          <Typography>
            Typing three or more characters presents the search assist menu.
          </Typography>
          <Typography>
            Note: Unlike Standard Search, Dynamic Search includes a
            &quot;clear entry&quot; button and not a Search button and the input
            field is 40px tall.
          </Typography>
          <Typography>
            The clear button will show up if users entered a keyword entry.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Search aria-label="Demo Example 5" dynamic>
            <Input
              aria-label="Site-wide"
              name="q"
              onChange={e => setStateThree({ value: e.target.value })}
              placeholder="Search locations"
              type="search"
              useNativePlaceholder
              value={stateThree.value}
              ref={refThree}
            />
            <SearchIcon>
              <Icon name="search" resolution="low" />
            </SearchIcon>
            {
              stateThree.value.length
                ? <SearchClearButton>
                  <ButtonIcon
                    ariaLabel="clear entry"
                    iconType="light-tiny"
                    onClick={handleClickThree}
                  >
                    <Icon name="clear" resolution="tiny" />
                  </ButtonIcon>
                </SearchClearButton>
                : undefined
            }
          </Search>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Contextual Search</Typography>
          <Typography tag="h3">Component Only (no search functionality)</Typography>
          <Typography>
            As the search scope becomes more narrow and contextual,
            the placement of the search feature will be placed closer with
            the content that it will search. This will utilize the text field,
            hidden label variant. The search feature may run dynamically
            depending on the app architecture.
          </Typography>
          <Typography>
            Typing three or more characters presents the search assist menu.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Search aria-label="Demo Example 6" dynamic>
            <Input
              aria-label="Site-wide"
              name="q"
              onChange={e => setStateFour({ value: e.target.value })}
              placeholder="Search news articles"
              type="search"
              useNativePlaceholder
              value={stateFour.value}
              ref={refFour}
            />
            <SearchIcon>
              <Icon name="search" resolution="low" />
            </SearchIcon>
            {
              stateFour.value.length
                ? <SearchClearButton>
                  <ButtonIcon
                    ariaLabel="clear entry"
                    iconType="light-tiny"
                    onClick={handleClickFour}
                  >
                    <Icon name="clear" resolution="tiny" />
                  </ButtonIcon>
                </SearchClearButton>
                : undefined
            }
          </Search>
        </Col>
      </Row>
    </Grid>
  );
};

export default SearchExample;
