import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-search-clear-btn';

const SearchClearButton = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    style={{ display: 'block' }}
    {...remainingProps}
  >
    {children}
  </div>
);

SearchClearButton.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

SearchClearButton.displayName = 'SearchClearButton';

export default SearchClearButton;
